<template>
<div class="toast-container position-fixed bottom-0 end-0 mb-2"
style="z-index:1999">
  <div
    v-for="(msg, key) in messages"
    :key="key"
    class="toast show"
    :class="`toast${key}`"
    role="alert"
  >
    <div class="toast-header">
      <span
        :class="`bg-${msg.style}`"
        class="p-2 me-2 d-inline-block"
      ></span>
      <strong class="me-auto">{{ msg.title }}</strong>
      <button
        type="button"
        class="btn-close"
        @click="clearToast(key)"
        aria-label="Close"
      ></button>
    </div>
    <div class="toast-body" v-if="msg.content">
      {{ msg.content }}
    </div>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      messages: []
      // messages: [
      //   {
      //     style: 'success', ( bootstrap 樣式 )
      //     title: '通知標題',
      //     content: '通知內容'
      //   }
      // ]
    }
  },
  inject: ['emitter'],
  methods: {
    toastShow () {
      setTimeout(() => {
        this.messages.shift()
      }, 6000)
    },
    clearToast (index) {
      this.messages.splice(index, 1)
    }
  },
  mounted () {
    this.emitter.on('push-message', (message) => {
      const { style = 'success', title, content } = message
      this.messages.push({ style, title, content })
      this.toastShow()
    })
  }
}
</script>
